import Vue from 'vue';
import Router from 'vue-router';
import AppIndex from '@/components/main/index.vue';
import AppBlog from '@/components/main/blog.vue';

Vue.use(Router);
let route = new Router({
    routes: [{
        path: '/',
        name: 'index',
        component: AppIndex
    }],
})
async function getRouter() {
    const getData = () => {
        return new Promise((resolve, reject) => {
            $.get('./static/resource/main.json', (req) => {
                resolve(req);
            });
        })
    }
    const getDate = (val) => {

        const padDate = function(val) {
            val = val < 10 ? '0' + val : val;
            return val;
        }

        var value = new Date(val);
        var year = value.getFullYear();
        var month = padDate(value.getMonth() + 1);
        var day = padDate(value.getDate());
        return year + '/' + month + '/' + day;
    }
    const data = await getData();
    data.map(item => {
        route.addRoutes([{
            path: `/${getDate(item.createdAt)}/${item.title}`,
            name: 'AppBlog-'+ item.title,
            component: AppBlog
        }]);
    });
}
getRouter();
export default route


