<template>
  <div class="sidebar-wrap">
    <div
      class="toggle-sidebar-info btn btn-hover"
      v-show="showMenu"
      @click="traggleMenu = !traggleMenu"
    >
      <span>{{traggleMenu?"站点概览":"文章目录"}}</span>
    </div>
    <div v-show="showMenu && traggleMenu">
      <div class="sidebar-doc">
        <h2 class="sidebar-doc-title">目录</h2>
        <div class="sidebar-doc-progress">
          <span class="progress-notice">您已阅读了</span>
          <span class="progress-num">{{percentNum}}</span>
          <span class="progress-percent">%</span>
          <div class="progress-bar" :style="'width:'+percent+'%'"></div>
        </div>
      </div>
      <div class="sidebar-doc-content" id="post">
        <my-menu :menu="menu"></my-menu>
      </div>
    </div>
    <div v-show="showMenu ? !traggleMenu:true" class="author-info">
      <div class="author-info-avatar">
        <img
          class="author-info-avatar-img"
          src="https://avatars1.githubusercontent.com/u/27287400?s=460&v=4"
          alt
        >
      </div>
      <div class="author-info-name">万军帅</div>
      <div class="author-info-description">虽千万人吾亦往矣</div>
      <div class="links-buttons">
        <a href="https://github.com/laklee" target="_blank" class="btn-hover btn link-button">
          <i class="dot dot-1"></i>GitHub
        </a>
        <a href="mailto:laklee@163.com" target="_blank" class="btn-hover btn link-button">
          <i class="dot dot-2"></i>E-Mail
        </a>
        <a
          href="tencent://message/?uin=904788661&Site=&Menu=yes"
          target="_blank"
          class="btn-hover btn link-button"
        >
          <i class="dot dot-3"></i>QQ
        </a>
      </div>
      <div class="author-info-article">
        <a href="#" class="article-archives color-gradual article-meta">
          <span>日志</span>
          <span>{{archives}}</span>
        </a>
        <a href="#" class="article-tags color-gradual article-meta">
          <span>标签</span>
          <span>{{tag}}</span>
        </a>
        <a href="#" class="color-gradual article-meta">
          <span>分类</span>
          <span>{{category}}</span>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import { TweenLite } from "gsap/TweenMax";
import MyMenu from "@/components/public/menu.vue";
export default {
  props: {
    percent: Number,
    actived: String,
    blogs: {
      type: Array,
      default: []
    },
    showMenu: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      tweenedNumber: 0,
      menu: [],
      traggleMenu: true,
      archives: 0,
      tag: 0,
      category: 0
    };
  },
  components: {
    MyMenu
  },
  computed: {
    percentNum() {
      return this.tweenedNumber.toFixed(0);
    }
  },
  watch: {
    percent(newValue) {
      TweenLite.to(this.$data, 2, { tweenedNumber: newValue });
    },
    actived(val) {
      if (!val) {
        $(".toc .toc-item")
          .find(".toc-item-child")
          .fadeOut();
      } else {
        let $this = $('.sidebar-doc-content .toc-link[href="#' + val + '"]');
        $this
          .parent()
          .find(".toc-item-child")
          .fadeIn();
      }
    }
  },
  async updated() {
    this.blogs = await this.getBlogs();
    this.archives = this.blogs.length;
      const tags = this.blogs.reduce((arr, item) => [...arr, ...item.tag], []);
      this.tag = Array.from(new Set(tags)).length;
      const category = this.blogs.reduce(
        (arr, item) => [...arr, ...item.category],
        []
      );
      this.category = Array.from(new Set(category)).length;
    if (this.$route.name.indexOf("AppBlog") > -1) {
      this.getAsideToc();
    }
  },
  async mounted() {
    this.blogs = await this.getBlogs();
    this.archives = this.blogs.length;
      const tags = this.blogs.reduce((arr, item) => [...arr, ...item.tag], []);
      this.tag = Array.from(new Set(tags)).length;
      const category = this.blogs.reduce(
        (arr, item) => [...arr, ...item.category],
        []
      );
      this.category = Array.from(new Set(category)).length;
    if (this.$route.name.indexOf("AppBlog") > -1) {
      this.getAsideToc();
    }
  },
  methods: {
    getBlogs() {
      const slef = this;
      return new Promise(resolve => {
        setTimeout(() => {
          resolve(slef.blogs);
        }, 1);
      });
    },
    async getAsideToc() {
      const title = this.$route.path.split("/").reverse()[0];
      const content = this.blogs.filter(item => item.title == title)[0];
      const menu = [];
      
      let currentMenu = menu.length;
      const div = document.createElement("div");
      $(div).html(content.content);
      const list = $(div).find("h2,h3,h4,h5,h6");
      list.each((idx, item) => {
        if (
          $(item)
            .prop("tagName")
            .toLowerCase() === "h2"
        ) {
          menu.push({
            title: $(item).text(),
            id: $(item).attr("id") || $(item).html(),
            children: []
          });
          currentMenu = menu.length;
        }
        if (
          $(item)
            .prop("tagName")
            .toLowerCase() === "h3"
        ) {
          menu[currentMenu - 1].children.push({
            title: $(item).text(),
            id: $(item).attr("id") || $(item).html(),
            children: []
          });
        }
      });
      this.menu = menu;
    }
  }
};
</script>

<style lang="scss">
@import "../../assets/css/public/sidebar.scss";
</style>




