<template>
  <div>
    <div class="content-inner">
      <my-article v-bind:item="content"></my-article>
    </div>
    <div class="pagination" v-if="content">
      <div class="prev-post pull-left" v-if="idx>0">
        <div class="prev-post-inner">
          <router-link
            :to="'/'+setDate(blogs[idx-1].createdAt)+'/'+blogs[idx-1].title"
          >{{blogs[idx-1].title}}</router-link>
        </div>
      </div>
      <div class="prev-post pull-right" v-if="idx<blogs.length - 1">
        <div class="prev-post-inner">
          <router-link
            :to="'/'+setDate(blogs[idx+1].createdAt)+'/'+blogs[idx+1].title"
          >{{blogs[idx+1].title}}</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MyArticle from "@/components/main/comment.vue";
export default {
  components: {
    MyArticle
  },
  data() {
    return {
      content: {},
      idx: 0
    };
  },
  props: {
    blogs: {
      type: Array,
      default: []
    }
  },
  watch: {
    $route(to, form) {
      this.loadBlog();
    }
  },
  methods: {
    setDate(val) {
      const padDate = function(val) {
        val = val < 10 ? "0" + val : val;
        return val;
      };

      var value = new Date(val);
      var year = value.getFullYear();
      var month = padDate(value.getMonth() + 1);
      var day = padDate(value.getDate());
      return year + "/" + month + "/" + day;
    },
    async loadBlog() {
      const title = this.$route.path.split("/").reverse()[0];
      const slef = this;
      function getBlogs() {
        return new Promise(resolve => {
          setTimeout(() => {
            resolve(slef.blogs);
          }, 1);
        });
      }
      this.blogs = await getBlogs();
      const content = this.blogs.filter(item => item.title == title)[0];
      this.idx = this.blogs.indexOf(content);
      this.content = content;
    }
  },
  updated(){
    this.loadBlog();
  },
  async mounted() {
    this.loadBlog();
  }
};
</script>

<style lang="scss">
@import "@/assets/css/main/index.scss";
</style>



