        <my-article v-bind:item="item" v-bind:isLink="true"></my-article>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import MyArticle from "@/components/main/comment.vue";

export default {
  data() {
    return {
      data: []
    };
  },
  props: {
    blogs: {
      type: Array,
      default: []
    }
  },
  components: {
    MyArticle
  },
  mounted() {
    console.log(this.blogs);
  }
};
</script>
<style lang="scss">
@import "@/assets/css/main/index.scss";
</style>




