const state = () => ({
    blog: []
});

const mutations = {
    setBlog(state, val) {
        state.blog = val;
    }
}
const actions = {
    setBlog: ({
        commit
    }, blog) => {
        commit('setBlog', blog)
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}


