<template>
  <transition appear>
    <div class="content-item" v-highlightA v-if="item.updatedAt">
      <div v-if="isLink ">
        <router-link
          class="content-title color-gradual"
          :to="setDate(item.createdAt)+'/'+item.title"
        >{{item.title}}</router-link>
      </div>
      <div v-if="!isLink">
        <a href="javascript:void(0)" class="content-title color-gradual">{{item.title}}</a>
      </div>
      <div class="content-tag">
        <time class="btn-hover btn">
          <i class="el-icon-date"></i>
          &nbsp;
          更新于{{ item.updatedAt | formDate}}
        </time>
        <div class="btn-hover btn">
          <span v-for="(c,idx) in item.category" :key="idx">
            <i class="iconfont icon-boxfuben"></i>
            <a href="./" class="link-a color-gradual">{{c}}</a>
            <i v-if="(idx % 2) === 0">&nbsp;| &nbsp;</i>
          </span>
        </div>
        <div class="btn-hover btn">
          <span v-for="(t,idx) in item.tag" :key="idx">
            <i class="iconfont icon-tag" style="vertical-align: -2px"></i>
            <a href="./" class="link-a color-gradual">{{t}}</a>
            <i class v-if="(idx % 2) === 0">&nbsp;| &nbsp;</i>
          </span>
        </div>
      </div>
      <div class="main-content" v-html="item.content"></div>
      <router-link
        v-if="isLink"
        :to="setDate(item.createdAt)+'/'+item.title"
        class="more btn-hover btn"
      >阅读全文</router-link>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    // 博客内容
    item: {
      type: Object,
      default: {}
    },
    isLink: false
  },
  updated() {
    this.formatHTML();
  },
  mounted() {
    this.formatHTML();
  },
  methods: {
    // 复制
    copy(el) {
      const selection = window.getSelection();
      const range = document.createRange();
      // 选取 所有的 code
      range.selectNodeContents(
        $(el.target)
          .parents("pre")
          .find("code")[0]
      );
      selection.removeAllRanges();
      // 将 code 添加到粘贴板上
      selection.addRange(range);
      const text = selection.toString();
      this.copyContent(text, el.target);
      selection.removeAllRanges();
    },
    // 格式html
    formatHTML() {
      const hs = $(this.$el).find("h2,h3,h4,h5");
      hs.each(function() {
        if (!$(this).attr("id")) {
          $(this).attr("id",  $(this).html());
        }
      });
      $(this.$el).find("pre").prepend(`<p class="code-header">
                  <span class="copy-notice"></span>
                  <i class="copy icon-clipboard iconfont color-gradual"></i>
                </p>`);
      $(this.$el)
        .find("ul")
        .addClass("ulist");
      $(this.$el)
        .find("ol")
        .addClass("olist");
      $(this.$el)
        .find("pre .copy")
        .on("click", this.copy);
      $(this.$el)
        .find("pre code")
        .each(function() {
          $(this).html(
            "<ul><li>" +
              $(this)
                .html()
                .trim()
                .replace(/<br>/g, "\n</li><li>") +
              "\n</li></ul>"
          );
        });
    },
    setDate(val) {
      const padDate = function(val) {
        val = val < 10 ? "0" + val : val;
        return val;
      };

      var value = new Date(val);
      var year = value.getFullYear();
      var month = padDate(value.getMonth() + 1);
      var day = padDate(value.getDate());
      return year + "/" + month + "/" + day;
    },
    // 复制成功
    copyContent(text, ctx) {
      if (
        document.queryCommandSupported &&
        document.queryCommandSupported("copy")
      ) {
        try {
          document.execCommand("copy"); // Security exception may be thrown by some browsers.
          $(ctx)
            .prev(".copy-notice")
            .text("复制成功")
            .animate({ marginRight: 30, opacity: 1 }, 750, () => {
              setTimeout(() => {
                $(ctx)
                  .prev(".copy-notice")
                  .animate({ marginRight: 0, opacity: 0 }, 750);
              }, 800);
            });
        } catch (ex) {
          $(ctx)
            .prev(".copy-notice")
            .text("复制失败")
            .animate({ marginRight: 30, opacity: 1 }, 750, () => {
              setTimeout(() => {
                $(ctx)
                  .prev(".copy-notice")
                  .animate({ marginRight: 0, opacity: 0 }, 750);
              }, 800);
            });
          return false;
        }
      } else {
        $(ctx)
          .prev(".copy-notice")
          .text("复制成功")
          .animate({ marginRight: 30, opacity: 1 }, 750, () => {
            setTimeout(() => {
              $(ctx)
                .prev(".copy-notice")
                .animate({ marginRight: 0, opacity: 0 }, 750);
            }, 800);
          });
      }
    }
  }
};
</script>




