<template>
  <ol class="toc">
    <li
      class="toc-item toc-level-2"
      v-for="(item,idx) in menu"
      :key="idx"
    >
      <a class="toc-link" @click.stop.prevent="selectActived($event)" :href="'#'+item.id">
        <span>{{index ? index + '.':''}}{{idx + 1}}.</span>
        <span>{{item.title}}</span>
      </a>
      <div class="toc-item-child" style="display:none;" v-if="item.children.length > 0">
        <my-menu :menu="item.children" :index="idx+1"></my-menu>
      </div>
    </li>
  </ol>
</template>

<script>
export default {
  name: "MyMenu",
  data() {
    return {
      activeName: 1
    };
  },
  props: {
    menu: {
      type: Array
    },
    index: Number
  },
  watch:{
    $route(to,form){
      // console.log(to);
    }
  },
  methods:{
    selectActived(ev){
      const id = $(ev.target).parent().attr('href');
      if(!id) return;
      document.querySelector(id).scrollIntoView(true);
    }
  }
};
</script>


