<template>
  <el-container id="app" class="container">
    <div class="canvas-wrap">
      <app-canvas/>
    </div>
    <transition appear v-on:appear="asideEnter" v-on:enter="asideEnter" v-on:leave="asideLeave">
      <el-aside v-if="show" class="aside">
        <app-aside :percent="percent" :showMenu="showMenu" :blogs="$store.state.blog.blog" :actived="actived"/>
      </el-aside>
    </transition>
    <el-container id="main" class="main">
      <el-header class="header">
        <app-header/>
      </el-header>
      <el-main class="main" style="scroll-behavior: smooth;" id="viewBox" ref="viewBox">
        <div id="box" class="content-outer">
          <router-view :blogs="$store.state.blog.blog"></router-view>
        </div>
        <transition>
          <div class="btn-hover btn" id="toTop" v-if="toTopShow" @click="toTop">
            <i class="el-icon-back" style="transform:rotate(90deg)"></i>
          </div>
        </transition>
      </el-main>
      <el-footer class="footer">
        <app-footer v-on:navevent="toggleNav()"/>
      </el-footer>
    </el-container>
  </el-container>
</template>

<script>
import AppCanvas from "./components/public/canvas.vue";
import AppAside from "./components/public/aside.vue";
import AppHeader from "./components/public/header.vue";
import AppFooter from "./components/public/footer.vue";

import Velocity from "velocity-animate";
import { TweenLite } from "gsap/TweenMax";

export default {
  elem: "#app",
  data() {
    return {
      show: false,
      transitionName: "slide-right",
      percent: 0,
      toTopShow: false,
      top: 0,
      actived:'',
      showMenu:false
    };
  },
  components: {
    AppCanvas,
    AppAside,
    AppHeader,
    AppFooter
  },
  mounted() {
    this.$store.dispatch("getData");
    const box = this.$refs.viewBox;
    box.$el.addEventListener("scroll", el => {
      const top = $(el.target).scrollTop();
      const height = $(el.target)
        .find("#box")
        .outerHeight(true);
      const boxHeight = $(el.target).height();
      this.percent = (top / (height - boxHeight)) * 100;
      this.findHeadPosititon();
      if (top > 100) {
        this.toTopShow = true;
        $("#toTop").css({ opacity: (top - 100) / 200 });
      } else {
        this.toTopShow = false;
      }
    });
  },
  watch: {
    $route(to, from) {
      if (to.name.indexOf("AppBlog") > -1 ) {
        this.show = true;
        this.showMenu = true;
        this.toTop();
      }
    },
    top(val) {
      $(this.$refs.viewBox.$el).scrollTop(val);
    }
  },
  methods: {
    asideEnter: function(el, done) {
      Velocity(el, "stop");
      Velocity(el, { left: 0 }, { duration: 800, easing: "spring" });
      const main = document.getElementById("main");
      Velocity(
        main,
        { marginLeft: "300px" },
        { duration: 800, easing: "spring" }
      );
    },
    asideLeave: function(el, done) {
      Velocity(el, "stop");
      Velocity(el, { left: "-300px" }, { duration: 800, easing: "spring" });
      const main = document.getElementById("main");
      Velocity(
        main,
        { marginLeft: "0px" },
        { duration: 800, easing: "spring" }
      );
    },
    toggleNav() {
      this.show = !this.show;
    },
    toTop() {
      this.top = $(this.$refs.viewBox.$el).scrollTop();
      TweenLite.to(this.$data, 0.5, { top: 0 });
    },
    findHeadPosititon() {
      let list = $("#box").find("h1,h2,h3,h4,h5,h6");
      let menuHeight = $("#menu-outer").outerHeight(true) + 5;
      let currentId = "";
      list.each(function() {
        if ($(this).offset().top <= menuHeight) {
          currentId = $(this).attr("id") || $(this).html();
        }
      });
      this.actived = currentId;
      $(".sidebar-doc-content .toc-link").removeClass("active");
      if (currentId === "") {
        return;
      }
      let $this = $(
        '.sidebar-doc-content .toc-link[href="#' + currentId + '"]'
      );
      $this.addClass("active");
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/app.scss";
</style>



