<template>
  <div class="wrap" id="menu-outer">
    <i class="el-icon-menu" @click="showTopNav"></i>
    <div class="top-nav hide-top-nav" id="topNav">
      <div>
        <a href="/" class="title-name color-gradual underline-leftToRight">首页</a>
        <a href="/tag" class="title-name color-gradual underline-leftToRight">标签</a>
      </div>
    </div>
    <div>
      <a href="javascript:void(0)" class="search title-name color-gradual underline-center">
        <i class="el-icon-search"></i>&nbsp;
        <span>搜索</span>
      </a>
      <a href="javascript:void(0)" class="title-name color-gradual underline-center">
        <span>个人主页</span>
      </a>
      <span id="time" style="margin-left:16px;"></span>
    </div>
  </div>
</template>

<script>
import Velocity from "velocity-animate";
export default {
  data() {
    return {
      navshow: true
    };
  },
  mounted() {
    const t = document.getElementById("time");
    let time = new Date();
    let hours = time.getHours();
    let minutes = time.getMinutes();
    let secconds = time.getSeconds();
    t.innerHTML = `${this.formatDate(hours)}:${this.formatDate(
      minutes
    )}:${this.formatDate(secconds)}`;
    setInterval(() => {
      time = new Date();
      hours = time.getHours();
      minutes = time.getMinutes();
      secconds = time.getSeconds();
      t.innerHTML = `${this.formatDate(hours)}:${this.formatDate(
        minutes
      )}:${this.formatDate(secconds)}`;
    }, 1000);
  },
  methods: {
    formatDate(num) {
      return num > 9 ? num : "0" + num;
    },
    showTopNav() {
      $("#topNav").toggleClass("hide-top-nav");
    }
  }
};
</script>

<style lang="scss">
@import "../../assets/css/public/header.scss";
</style>




