<template>
  <div class="footer-wrap">
    <transition appear mode="in-out" name="fade">
      <div class="btn-hover btn" v-if="show" id="sidebar" @click="sendEmit">
        <i class="el-icon-back"></i>
      </div>
      <div class="btn-hover btn" v-else id="sidebar" @click="sendEmit">
        <i class="el-icon-back" style="transform:rotate(180deg)"></i>
      </div>
    </transition>
  </div>
</template>

<script>
import Velocity from "velocity-animate";

export default {
  data() {
    return {
      show: true
    };
  },
  methods: {
    sendEmit(el) {
      this.show = !this.show;
      this.$emit("navevent");
    }
  }
};
</script>

<style lang="scss">
@import "../../assets/css/public/footer.scss";
</style>




