import Vue from 'vue';
import Vuex from 'vuex';
import blog from './blog/blog';
import axios from 'axios';
Vue.use(Vuex);

const store = new Vuex.Store({
    modules: {
        blog
    },
    actions: {
        async getData({
            commit
        }) {
            const {status,data} = await axios.get('./static/resource/main.json');
            commit('blog/setBlog',data);
        }
    }
});

export default store;


